@import url("https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap");

:root {
  /* HTML Element Default Style Configuration */
  --arcade-font-family: "Press Start 2P", system-ui;
  --header-text-font-family: "ClashDisplay-Light", system-ui;
  --header-highlight-text-font-family: "ClashDisplay-Regular", system-ui;
  --text-font-family: "Inter", "system-ui";
}

/* HTML Element Default Style Configuration */

html,
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  background-color: #000;
}

span,
p,
h1,
h2,
h3,
h4,
h5,
h6,
a {
  font-family: var(--text-font-family);
}

ul {
  list-style-type: none;
}

li {
  margin: 0;
  padding: 0;
}

.html-red {
  color: #ec7979;
}

.html-white {
  color: #ffffff;
}

.html-blue {
  color: #85ece0;
}

/* Font Load */

